.rights_modal .modal-content {
       border-radius: 0;
       height: auto;
       border: none;
}

.rights_modal_sidebar {
       margin: 0 auto;
       width: 100%;
       height: auto;
       background-color: var(--bs-body-bg);
}

.rights_modal_inner {
       height: auto;
       width: 100%;
}

.rights_modal_img_wrapper .object-center-cover {
       object-fit: contain;
}

.rights_modal_img_wrapper .rights_thumb .object-center-cover {
       object-fit: cover;
}

.rights_modal_arrow_right,
.rights_modal_arrow_left {
       height: 38px;
       width: 38px;
       opacity: .7;
}

.rights_modal_arrow_left {
       left: 6%;
}

.rights_modal_arrow_right {
       right: 6%;
}

.rights_account_row {
       margin-left: -0.25rem !important;
       margin-right: -0.25rem !important;
}

input[type="file"] {
       display: none;
}

.custom-file-upload {
       border: 1px solid #ccc;
       display: inline-block;
       padding: 6px 12px;
       cursor: pointer;
}

.rights_modal_img_wrapper {
       max-width: 450px;
       margin: 0 auto;
}

.rights_modal_img_wrapper .carousel-indicators {
       overflow: auto;
       justify-content: space-between;
}

.rights_modal_img_wrapper .carousel-control-prev,
.rights_modal_img_wrapper .carousel-control-next {
       height: auto;
       align-items: flex-start;
       justify-content: flex-start;
       width: auto;
       opacity: 1;
       top: 30%;
}

.rights_modal_img_wrapper .carousel-control-next {
       right: 0%;
}

.rights_modal_img_wrapper .carousel-control-prev {
       left: 0%;
}

.ugc-wizard-nav .nav-link.active {
       border-color: var(--bs-success);
}

.ugc-wizard-nav .checkIcoFilled,
.ugc-wizard-nav .active .checkIco {
       display: none;
}

.ugc-wizard-nav .active .checkIcoFilled,
.ugc-wizard-nav .checkIco {
       display: block;
}

.ugc-wizard-nav .active .checkIcoFilled {
       fill: var(--bs-success);
}

.ugc_add_logo .uppy-DragDrop-label {
       font-size: 0;
       margin-bottom: 10px;
}

.ugc_add_logo .uppy-DragDrop-label:before {
       content: 'Upload Logo';
       font-size: 14px;
}

.ugc_add_logo .uppy-DragDrop-inner:before {
       border: none
}

.rights_thumb .splide__track--nav>.splide__list>.splide__slide {
       opacity: 0.4;
       border: none;
}

.rights_thumb .splide__track--nav>.splide__list>.splide__slide.is-active {
       opacity: 1;
       border: none;
}

.ugc_logo_ {
       width: 200px;
       height: 80px;
       display: block;
       text-align: center;
}

.ugc_add_logo .uppy-DragDrop-inner {
       padding: 6px 20px;
}


.btn-active-danger.btn-outline-danger:hover {
       color: #fff !important;
       border-color: #fff !important;
}

.swal2-container .swal2-styled.swal2-confirm.btnInsta {
       padding: 2px;
       background: #f09433;
       background: -moz-linear-gradient(45deg, #f09433 0%, #e6683c 25%, #dc2743 50%, #cc2366 75%, #bc1888 100%);
       background: -webkit-linear-gradient(45deg, #f09433 0%, #e6683c 25%, #dc2743 50%, #cc2366 75%, #bc1888 100%);
       background: linear-gradient(45deg, #f09433 0%, #e6683c 25%, #dc2743 50%, #cc2366 75%, #bc1888 100%);
       border: 1px solid #e2306c;
       transition: all .3s ease-in-out;
}

.btnInsta:after {
       content: "OR";
       position: absolute;
       bottom: -33px;
       background-color: #fff;
       height: 20px;
       width: 60px;
       right: 0;
       left: 0;
       margin: 0 auto;
       color: #959595;
}

.btnInsta:before {
       content: "";
       position: absolute;
       bottom: -23px;
       right: 0;
       left: 0;
       height: 1px;
       width: 100%;
       background-color: #959595;
}

.swal2-container .swal2-styled.swal2-confirm.btnInsta:active,
.swal2-container .swal2-styled.swal2-confirm.btnInsta:focus,
.swal2-container .swal2-styled.swal2-confirm.btnInsta:hover {
       background: #f09433;
       background: linear-gradient(45deg, #f09433, #e6683c 25%, #dc2743 50%, #cc2366 75%, #bc1888);
       border: 1px solid #e2306c;
       box-shadow: 0 1px 3px rgba(226, 48, 108, .5);
}

.swal2-container .swal2-styled.swal2-cancel.btnTiktok {
       padding: 2px;
       background: #000000;
       border: 1px solid #000000;
       transition: all .3s ease-in-out;
}

.swal2-container .swal2-styled.swal2-cancel.btnTiktok:active,
.swal2-container .swal2-styled.swal2-cancel.btnTiktok:focus,
.swal2-container .swal2-styled.swal2-cancel.btnTiktok:hover {
       background: #000000;
       border: 1px solid #000000;
       box-shadow: 0 1px 3px rgba(0, 0, 0, .5);
}

.swal2-container .swal2-styled.swal2-confirm.errorBtnInsta {
       padding: 2px;
       background: #f09433;
       background: -moz-linear-gradient(45deg, #f09433 0%, #e6683c 25%, #dc2743 50%, #cc2366 75%, #bc1888 100%);
       background: -webkit-linear-gradient(45deg, #f09433 0%, #e6683c 25%, #dc2743 50%, #cc2366 75%, #bc1888 100%);
       background: linear-gradient(45deg, #f09433 0%, #e6683c 25%, #dc2743 50%, #cc2366 75%, #bc1888 100%);
       border: 1px solid #e2306c;
       transition: all .3s ease-in-out;
}

.swal2-container .swal2-styled.swal2-confirm.errorBtnInsta:active,
.swal2-container .swal2-styled.swal2-confirm.errorBtnInsta:focus,
.swal2-container .swal2-styled.swal2-confirm.errorBtnInsta:hover {
       background: #f09433;
       background: linear-gradient(45deg, #f09433, #e6683c 25%, #dc2743 50%, #cc2366 75%, #bc1888);
       border: 1px solid #e2306c;
       box-shadow: 0 1px 3px rgba(226, 48, 108, .5);
}

.swal2-container .swal2-styled.swal2-confirm.errorBtnTiktok {
       padding: 2px;
       background: #000000;
       border: 1px solid #000000;
       transition: all .3s ease-in-out;
}

.swal2-container .swal2-styled.swal2-confirm.errorBtnTiktok:active,
.swal2-container .swal2-styled.swal2-confirm.errorBtnTiktok:focus,
.swal2-container .swal2-styled.swal2-confirm.errorBtnTiktok:hover {
