.assets_modal .modal-content{
    border-radius: 0;
    height: auto;
    border: none;
}
.full_modal_sidebar{
    max-width: 100%;
    width: 100%;
    height: auto;
    overflow-y: auto;
    background-color: var(--bs-body-bg);
}

.assets_modal_inner {
    height: auto;
}
.assets_modal_img_outer{
    height: auto;
    width: 100%;
}

.assets_modal_img_wrapper{
    width: 100%;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
}

.assets_modal_img_wrapper video,
.assets_modal_img_wrapper iframe{
    background-color: #000;
    height: 100%;
    width: 100%;
    object-fit: contain;
    display: flex;
    align-items: center;
}
.assets_modal_media__{
    max-width: 100%;
    height: auto;
    display: block;
    cursor: zoom-in !important;
}
.assets_modal_arrow_right,
.assets_modal_arrow_left{
    height: 38px;
    width: 38px;
    opacity: .7;
    z-index: 99;
}

.tab_product_dropdown_list{
    max-height: 270px;
    overflow-x: auto;
}

.assets_modal_inner .splide__pagination,
.assets_modal_inner .splide__arrow {
    z-index: 2;
}

.assets_modal_arrow_left{
    left: 30px;
}

.assets_modal_arrow_right{
    right: 30px;
}

.m_c_p .splide__arrow--next{
    right: 80px;
}
.m_c_p .splide__arrow--prev{
    left: 80px;
}

.p_tags_height{
    min-height: calc(100vh - 294px);
}

@media (min-width: 992px){
    .assets_modal_inner {
        width: calc(100% - 400px);
        height: calc(100vh - 46px);
    }

    .assets_modal .modal-content{
        height: 100vh;
    }
    .assets_m_header_sidebar{
        width: 100%;
    }
    .assets_modal_inner {
        height: calc(100vh - 46px);
    }
    .assets_modal_arrow_right,
    .assets_modal_arrow_left{
        height: 50px;
        width: 50px;
    }
    .assets_modal_img_outer{
        height: 80vh;
        max-width: 120vh;
        width: 100%;
    }
    .assets_modal_img_wrapper{
        height: 100%;
    }
    .assets_modal_media__{
        position: absolute;
        top: 0;
        bottom:0;
        right: 0;
        left: 0;
    }
    .assets_modal.modal{
        overflow: hidden;
        z-index: 95;
    }
    .full_modal_sidebar, .assets_m_header_sidebar{
        max-width: 370px;
        border-left: var(--bs-border-width) var(--bs-border-style) var(--bs-border-color) !important;
    }
    .assets_modal_inner {
        width: calc(100% - 370px);
        height: calc(100vh - 98px);
    }
    .assets_modal_full .assets_modal_inner{
        height: calc(100vh - 46px);
    }
    .m_sidebar_scroll_{
        height: calc(100vh - 195px);
        overflow-x: auto;
    }

    .m_sideCont_scroll_{
        height: calc(100vh - 142px);
        overflow-x: auto;
    }
    .assets_modal.modal {
        height: calc(100vh - 52px);
        width: calc(100vw - 84px);
        bottom: 0;
        top: auto;
        left: auto;
        right: 0;
    }
    .assets_modal_full.assets_modal.modal{
        height: 100%;
        width: 100%;

    }
}

@media (min-width: 1600px){
    .full_modal_sidebar, .assets_m_header_sidebar{
        max-width: 400px;
        border-left: var(--bs-border-width) var(--bs-border-style) var(--bs-border-color) !important;
    }
    .assets_modal_inner {
        width: calc(100% - 400px);
        height: calc(100vh - 98px);
    }
    
}

@media (max-width: 991px){
.assets_modal .modal-body{
    flex: auto;
}

.assets_modal_arrow_left{
    left: 10px;
}

.assets_modal_arrow_right{
    right: 10px;
}

.m_c_p .splide__arrow--next{
    right: 60px;
}
.m_c_p .splide__arrow--prev{
    left: 60px;
}
}